﻿html, body {
	outline:	none;
}

a {
	color:	#2196F3;
}
[ng-click] {
	cursor:		pointer;
	outline:	none;
}


::-webkit-scrollbar {
	background-color:	rgba(0,0,0,0.1);
	width:				6px;
}

::-webkit-scrollbar-thumb:window-inactive,
::-webkit-scrollbar-thumb {
	background:  	rgba(255,255,255,0.2);
	border-radius:	3px;
	margin:			1px;
	box-shadow:		0 0 5px rgba(0,0,0,0.3);
}

.window-controls {
	position:			absolute;
	z-index:			50000;
	top:				0;
	right:				0;
	display:			table;
	background-color:	rgba(255,255,255,0.05);
	border-radius:		0 0 0 5px;
	& > div {
		display:	table-row;
		& > div {
			display:	table-cell;
			padding:	4px 10px;
			outline:	none;
			&:first-child {
				border-radius:		0 0 0 5px;
			}
			&.devTools {
				&:hover {
					background-color:	rgba(251, 140, 0,0.5);
				}
			}
			&.minimize {
				&:hover {
					background-color:	rgba(255,255,255,0.1);
				}
			}
			&.maximize {
				&:hover {
					background-color:	rgba(3, 155, 229,0.5);
				}
			}
			&.close {
				&:hover {
					background-color:	rgba(229, 57, 53,0.5);
				}
			}
		}
	}
}

.sidebar-menu {
	display:		table;
	width:			100%;
	table-layout:	fixed;
	outline:		none;
	& > div.action {
		display:	table-row;
		& > div {
			display:		table-cell;
			padding:		10px 10px;
			box-sizing:		border-box;
			text-overflow:	ellipsis;
			overflow:		hidden;
			white-space:	nowrap;
			font-size:		13px;
			font-weight:	400;
			&.icon {
				width:			32px;
				text-align:		right;
			}
		}
	}
	& > div.terminal {
		display:	table-row;
		& > div {
			display:		table-cell;
			vertical-align:	middle;
			padding:		10px 5px;
			box-sizing:		border-box;
			text-overflow:	ellipsis;
			overflow:		hidden;
			white-space:	nowrap;
			font-size:		13px;
			font-weight:	400;
			&.status {
				width:			16px;
				text-align:		center;
				padding:		0 5px;
				& > div {
					width:			6px;
					height:			6px;
					border-radius:	50%;
					background-color:	rgba(255,255,255, 0.3);
					&.active {
						background-color:	#8BC34A;
						box-shadow:			0 0 5px rgba(0,0,0,0.9);
					}
					&.error {
						background-color:	#C62828;
						box-shadow:			0 0 5px rgba(0,0,0,0.9);
					}
				}
			}
			&.icon {
				width:			32px;
				text-align:		right;
			}
			.tag {
				background-color:	rgba(3, 155, 229, 0.7);
				color:				#ffffff;
				font-size:			9;
				padding:			2px 4px;
				border-radius:		3px;
			}
			&.port {
				color:			#8BC34A;
				text-shadow:	0 0 5px rgba(0,0,0,0.5);
				font-size:		10px;
				font-weight:	700;
				width:			50px;
				text-align:		right;
			}
		}
	}
	&:hover {
		background-color:	rgba(0,0,0,0.1);
	}
	&.active {
		background-color:	rgba(3, 155, 229, 0.2);
	}
}


.status-dot {
	display:		inline-block;
	width:			6px;
	height:			6px;
	border-radius:	50%;
	vertical-align:	middle;
	background-color:	rgba(255,255,255, 0.3);
	&.active {
		background-color:	#8BC34A;
		box-shadow:			0 0 5px rgba(0,0,0,0.9);
	}
	&.error {
		background-color:	#C62828;
		box-shadow:			0 0 5px rgba(0,0,0,0.9);
	}
}