.app-shell {
	display:		table;
	width:			100%;
	table-layout:	fixed;
	& > div {
		display:	table-row;
		& > div {
			display:	table-cell;
			padding:	2px;
			&.time {
				text-align:		right;
				font-size:		10px;
				font-weight:	700;
				color:			#ffffff;
				width:			50px;
				&.loading {
					
				}
			}
			&.output {
				& > .cmd {
					text-align:		left;
					padding:		2px 5px;
					font-size:		11px;
					font-weight:	700;
					color:			#ffffff;
					word-wrap:		break-word;
					white-space:	pre-wrap;
					font-family:	monospace;
					text-shadow:	0 0 5px rgba(0,0,0,0.5);
				}
				& > .logs {
					border-radius:	10px;
					background:		linear-gradient(to left, rgba(0,0,0,0.1), rgba(255,255,255,0.05));
					padding:		10px;
					margin:			10px;
					text-shadow:	0 0 5px rgba(0,0,0,0.5);
					& > .log {
						text-align:		left;
						overflow-wrap:	break-word;
						overflow-x:		auto;
						width:			100%;
						margin:			0;
						background-color:	transparent;
						font-size:		10px;
						font-weight:	400;
						color:			rgba(255,255,255,0.9);
						word-wrap:		break-word;
						white-space:	pre-wrap;
						font-family:	monospace;
						&.error {
							color:	#C62828;
						}
					}
					.error-message {
						background-color:	rgba(255,0,0, 0.3);
						color:				#ffffff;
						font-size:			10px;
						font-weight:		400;
						padding:			5px;
						display:			inline-block;
						border-radius:		3px;
					}
				}
			}
		}
	}
}


.tag {
	background-color:	rgba(3, 169, 244, 0.7);
	color:				#ffffff;
	font-size:			9px;
	padding:			3px 5px;
	border-radius:		3px;
	vertical-align:		middle;
	white-space:		nowrap;
	display:			inline-block;
	margin:				2px;
	line-height:		12px;
	&.green {
		background-color:	rgba(102, 187, 106, 0.7);
	}
	&.orange {
		background-color:	rgba(255, 143, 0, 0.7);
	}
	&.red {
		background-color:	rgba(211, 47, 47, 0.7);
	}
	&.blue {
		background-color:	rgba(3, 169, 244, 0.7);
	}
	&.gray {
		background-color:	rgba(120, 144, 156, 0.7);
	}
	&.disabled {
		background-color:	rgba(0, 0, 0, 0.3);
		color:				rgba(0, 0, 0, 0.7);
	}
	
	& > span {
		display:			inline-block;
		/*vertical-align:		middle;*/
	}
	& > .label {
		white-space:		nowrap;
		text-overflow:		ellipsis;
		overflow:			hidden;
		max-width:			120px;
		vertical-align:		middle;
	}
	.disabled & {
		opacity:			0.2;
	}
}




.ui-table {
	max-height:			200px;
	overflow-y:			auto;
	&.no-scroll {
		max-height:		none;
		overflow-y:		visible;
	}
	& > .title {
		font-size:		16px;
		font-weight:	600;
		letter-spacing:	1px;
		color:			#ffffff;
		padding:		5px;
	}
	& > table {
		width:			100%;
		table-layout:	fixed;
		& > thead {
			& > tr {
				& > th {
					padding:		10px 5px;
					font-size:		12px;
					font-weight:	500;
					color:			rgba(255,255,255,0.6);
					text-align:		left;
					&.left {
						text-align:		left !important;
					}
					&.center {
						text-align:		center !important;
					}
					&.right {
						text-align:		right !important;
					}
				}
			}
		}
		& > tbody {
			& > tr {
				background-color:	rgba(255,255,255,0.05);
				& > td {
					cursor:			default;
					padding:		5px;
					font-size:		13px;
					font-weight:	500;
					color:			rgba(255,255,255,0.9);
					text-align:		left;
					vertical-align:	middle;
					.title {
						font-size:		14px;
						font-weight:	700;
						line-height:	22px;
					}
					.info {
						font-size:		10px;
						font-weight:	400;
						line-height:	16px;
						color:			rgba(255,255,255,0.5);
					}
					
					
					
					&.ellipsis {
						white-space:	nowrap;
						overflow:		hidden;
						text-overflow:	ellipsis;
					}
					&.icon {
						width:			30px;
						font-size:		16px;
						&.disabled {
							color:			rgba(0, 0, 0, 0.2);
						}
					}
					&.icon-action {
						padding:		1px;
						width:			20px;
						font-size:		12px;
						color:			rgba(2, 119, 189, 1);
						&:hover {
							background-color:	darken(rgba(66, 165, 245, 1), 10%);
							color:				#ffffff;
							text-decoration:	none;
						}
						&.disabled {
							color:			rgba(0, 0, 0, 0.2);
							&:hover {
								background-color:	transparent;
							}
						}
					}
					&.name {
						width:			120px;
						max-width:		120px;
						font-weight:	600;
					}
					&.shortcode {
						max-width:		40px;;
					}
					&.date {
						width:			110px;
						max-width:		110px;
					}
					&.ago {
						width:			80px;
						max-width:		80px;
					}
					&.profile {
						width:			200px;
					}
					.small-info {
						font-size:		11px;
						color:			rgba(0,0,0,0.4);
						font-weight:	400;
					}
					&.left {
						text-align:		left !important;
					}
					&.center {
						text-align:		center !important;
					}
					&.right {
						text-align:		right !important;
					}
					&.small {
						font-size:		11px;
					}
					&.number {
						width:			100px;
						text-align:		right;
					}
					&.nobreak {
						white-space:	nowrap;
					}
					&.bold {
						font-weight:	800;
					}
					&.action {
						text-align:		right;
						& > span {
							&.link {
								color:			rgba(66, 165, 245, 0.6);
								font-weight:	600;
								padding:		0 5px;
								transition:		color ease-out 0.3s;
								&:hover {
									color:			rgba(66, 165, 245, 1);
								}
							}
						}
						& > .btn {
							cursor:				pointer;
							display:			inline-block;
							vertical-align:		middle;
							text-decoration:	none;
							border-radius:		5px;
							background-color:	rgba(66, 165, 245, 1);
							color:				#ffffff;
							font-weight:		600;
							box-shadow:			1px 1px 5px rgba(0, 0, 0, 0.2);
							transition:			transform 0.2s ease-out, background-color 0.2s ease-out, color 0.2s ease-out;
							font-size:			12px;
							line-height:		35px;
							padding:			0 15px;
							margin:				5px;
							&:hover {
								background-color:	darken(rgba(66, 165, 245, 1), 10%);
								text-decoration:	none;
							}
						}
					}
					&.toggle {
						text-align:		right;
					}
					&.checkbox {
						text-align:		center;
						width:			20px;
					}
					.ago {
						font-size:		10px;
						color:			rgba(0,0,0,0.6);
						line-height:	15px;
						font-weight:	500;
					}
					& > select {
						width:			100%;
						padding:		5px;
						border-radius:	3px;
						border:			1px solid rgba(0,0,0,0.5);
						& > option {
							white-space:	nowrap;
							overflow:		hidden;
							text-overflow:	ellipsis;
						}
					}
					&.disabled {
						color:	rgba(0,0,0,0.2);
						& > select {
							color:	rgba(0,0,0,0.2);
							border:	1px solid rgba(0,0,0,0.2);
						}
					}
					&.error {
						background-color:	rgba(198, 40, 40, 0.5);
						color:				#ffffff;
					}
				}
				@border-radius:	7px;
				&:first-child {
					border-radius:			@border-radius @border-radius 0 0;
					& > td:first-child {
						border-radius:		@border-radius 0 0 0;
					}
					& > td:last-child {
						border-radius:		0 @border-radius 0 0;
					}
				}
				&:last-child {
					border-radius:			0 0 @border-radius @border-radius;
					& > td:first-child {
						border-radius:		0 0 0 @border-radius;
					}
					& > td:last-child {
						border-radius:		0 0 @border-radius 0;
					}
				}
				& > td:first-child {
					padding-left:		20px;
				}
				& > td:last-child {
					padding-right:		20px;
				}
				/*background-color:	rgba(33, 150, 243, 0);*/
				transition:			background-color ease-out 0.3s;
				
				&:nth-child(even) {
					& > td {
						background-color:	rgba(0, 0, 0, 0.1);
					}
				}
				&:hover {
					& > td {
						background-color:	rgba(33, 150, 243, 0.3);
					}
				}
				
				&.selected {
					background-color:	rgba(66, 165, 245, 0.3) !important;
					& > td {
						color:				#333333;
						background-color:	rgba(66, 165, 245, 0.3) !important;
					}
					&:hover {
						background-color:	rgba(66, 165, 245, 0.3) !important;
					}
				}
				&.no-hover {
					&:hover {
						background-color:	transparent;
						& > td {
							color:				#333333;
							background-color:	transparent !important;
						}
					}
				}
			}
		}
		& > tfoot {
			& > tr {
				& > td {
					text-align:	right;
				}
				& > td:first-child {
					padding-left:		20px;
				}
				& > td:last-child {
					padding-right:		20px;
				}
			}
		}
	}
	&.center {
		& > table {
			th {
				text-align:	center;
			}
			td {
				text-align:	center;
			}
		}
	}
	&.small {
		& > table {
			th {
				font-size:	11px;
			}
			td {
				font-size:	11px;
			}
		}
	}
	&.border {
		& > table {
			& > tbody {
				& > tr {
					& > td {
						border-right:	1px solid rgba(0,0,0,0.1);
						border-bottom:	1px solid rgba(0,0,0,0.1);
						&:last-child {
							border-right:	0;
						}
					}
				}
			}
		}
	}
	&.border-v {
		& > table {
			& > tbody {
				& > tr {
					& > td {
						border-bottom:	1px solid rgba(0,0,0,0.1);
					}
				}
			}
		}
	}
	&.stripped {
		& > table {
			& > tbody {
				& > tr {
					&:nth-child(even) {
						background-color:	rgba(0,0,0, 0.1);
					}
				}
			}
		}
	}
	&.dark-header {
		& > table {
			& > thead {
				& > tr {
					background-color:	#444B5A;
					color:				#ffffff;
				}
			}
		}
	}
	&.autosize {
		& > table {
			width:	auto;
			margin:	0 auto;
		}
	}
	&.rounded {
		@radius: 5px;
		background-color:	transparent;
		padding:			20px;
		& > table {
			background-color:	#ffffff;
			width:	auto;
			margin:	0 auto;
			border-radius:		@radius;
			box-shadow:			0 0 5px rgba(0,0,0,0.3);
			& > tbody {
				& > tr {
					&:first-child {
						& > td {
							&:first-child {
								border-radius:		@radius 0 0 0;
							}
							&:last-child {
								border-radius:		0 @radius 0 0;
							}
						}
					}
					&:last-child {
						& > td {
							&:first-child {
								border-radius:		0 0 0 @radius;
							}
							&:last-child {
								border-radius:		0 0 @radius 0;
							}
						}
					}
				}
			}
		}
	}
}



/*
	Copy-pasted from Quant-Studio.com with authorization
*/
.app-dialog {
	position:	fixed;
	top:		0;
	left:		0;
	width:		100%;
	height:		100%;
	display:	table /*!important*/;
	z-index:	51000;	/* navbar is 50000 */
	
	
	transition:	opacity ease-out 0.3s, transform ease-out 0.3s;
	
	&.ng-enter, &.ng-leave.ng-leave-active {
		opacity:	0;
	}
	&.ng-leave, &.ng-enter.ng-enter-active {
		opacity:	1;
	}
	
	&.front {
		z-index:	52000;
	}

	/* Dialog bg */
	& > .bg {
		position:	absolute;
		top:		0;
		left:		0;
		width:		100%;
		height:		100%;
		background-color:	rgba(0,0,0,0.7);
	}
	.overlay {
		position:		relative;
		vertical-align:	middle;
		text-align:		center;
		display:		table-cell;
		
		& > div {
			max-width:		500px;
			padding:		60px;
			margin:			0 auto;
			text-align:		left;
			border-radius:	5px;
			.dialog-close {
				color:		#ffffff;
				text-align:	right;
				margin:		10px 0;
				font-size:	12px;
			}
		}
		&.form {
			& > div {
				max-width:		500px;
			}
		}
		&.medium {
			& > div {
				max-width:		500px;
			}
		}
		&.large {
			& > div {
				max-width:		800px;
			}
		}
	}
	&.small {
		& > .overlay {
			& > div {
				max-width:		400px;
			}
		}
	}
	&.medium {
		& > .overlay {
			& > div {
				max-width:		600px;
			}
		}
	}
	&.large {
		.overlay {
			& > div {
				max-width:		800px;
			}
		}
	}
	&.x-large {
		& > .overlay {
			& > div {
				max-width:		90%;
			}
		}
	}
}


@color_dialog_header:			#1F2229;
@color_dialog_menu:				#2D2F3B;
@color_dialog_title:			#ffffff;
@color_dialog_body:				#37474F;
@color_dialog_text:				#ffffff;
@color_dialog_sep:				#20222E;
@color_dialog_cta:				#508ED7;
@color_dialog_cta2:				#5BB85D;
@color_cta:						#1CC5BC;

.app-dialog-box {
	background-color:	@color_dialog_body;
	border-radius:		5px;
	
	.message {
		text-align:			center;
		padding:			5px;
		font-size:			11px;
		background-color:	@color_dialog_cta;
		color:				@color_dialog_text;
		&.error {
			background-color:	#F44336;
			color:				#ffffff;
		}
		a {
			font-size:			10px;
			text-decoration:	underline;
		}
	}
	.header {
		background-color:	@color_dialog_header;
		color:				@color_dialog_title;
		font-size:			14px;
		font-weight:		700;
		padding:			10px;
		border-bottom:		1px solid @color_dialog_sep;
		border-radius:		5px 5px 0 0;
		text-align:			center;
		& > .author {
			color:			@color_cta;
			font-size:		11px;
			font-weight:	500;
		}
		&.with-tabs {
			position:	relative;
			.dialog-header-tabs {
				position:	absolute;
				top:		10px;
				right:		10px;
			}
		}
	}
	.menu {
		background-color:	@color_dialog_menu;
		color:				@color_dialog_text;
		font-size:			12px;
		font-weight:		500;
		padding:			5px;
		border-bottom:		1px solid @color_dialog_sep;
	}
	.tabs {
		background-color:	@color_dialog_menu;
		color:				@color_dialog_text;
		font-size:			12px;
		font-weight:		500;
		padding:			5px;
		border-bottom:		1px solid @color_dialog_sep;
		text-align:			center;
		& > div {
			display:		inline-block;
			padding:		5px 15px;
			margin:			4px 0;
			color:			darken(@color_dialog_text, 30%);
			border-bottom:	2px solid transparent;
			/*transition:		border-bottom 0.2s linear, color 0.3s linear;*/
			&.selected {
				border-bottom:	2px solid @color_dialog_cta;
				color:			@color_dialog_text;
			}
			&:hover {
				color:			@color_dialog_text;
			}
			.tag {
				font-size:		8px;
			}
		}
	}
	.body {
		background-color:	@color_dialog_body;
		color:				@color_dialog_text;
		font-size:			12px;
		font-weight:		500;
		padding:			10px;
		border-bottom:		1px solid @color_dialog_sep;
		border-radius:		0 0 5px 5px;
		&.no-padding {
			padding:		0;
		}
		&.scroll {
			max-height:	600px;
			overflow-y:	auto;
		}
	}
	
	.confirmation-message {
		padding:		20px;
		text-align:		center;
		font-size:		14px;
		font-weight:	500;
		color:			rgba(0,0,0,0.7);
	}
	
	
	&.static-height {
		.body {
			height:			400px;
			overflow-y:		auto;
		}
		
	}
	
	
	.text-block {
		color:				@color_dialog_text;
		font-size:			12px;
		font-weight:		500;
		padding:			10px;
		text-align:			left;
		&.center {
			text-align:		center;
		}
		&.right {
			text-align:		right;
		}
		a {
			color:				@color_dialog_cta;
			text-decoration:	underline;
		}
	}
	.footer {
		background-color:	@color_dialog_cta;
		border-top:			1px solid rgba(255,255,255,0.05);
		color:				@color_dialog_text;
		font-size:			12px;
		font-weight:		600;
		padding:			10px;
		text-align:			center;
		border-radius:		5px;
		margin:				5px 0;
		&:last-child {
			margin:			5px 0 0 0;
		}
		&:hover {
			background-color:	darken(@color_dialog_cta, 5%);
		}
		&.green {
			background-color:	@color_dialog_cta2;
			&:hover {
				background-color:	darken(@color_dialog_cta2, 5%);
			}
		}
		&.disabled, &.disabled:hover {
			background-color:	rgba(0,0,0,0.2);
			color:				rgba(255,255,255,0.2);
			cursor:				default;
		}
	}
	.text-link {
		color:				@color_dialog_text;
		font-size:			11px;
		font-weight:		300;
		padding:			10px;
		text-align:			center;
		margin:				5px 0;
	}
}